import kaplay from "kaplay";
import { createPlayer } from "./player";
// import "kaplay/global"; // uncomment if you want to use without the k. prefix

const k = kaplay({
  width: 1280,
  height: 1024,
  buttons: {
    next: {
      keyboard: "space",
      mouse: "left",
    },
    up: {
      keyboard: ["w", "up"],
      gamepad: ["dpad-up"] // You can also bind "leftstick-up" if supported
    },
    down: {
      keyboard: ["s", "down"],
      gamepad: ["dpad-down"]
    },
    left: {
      keyboard: ["a", "left"],
      gamepad: ["dpad-left"]
    },
    right: {
      keyboard: ["d", "right"],
      gamepad: ["dpad-right"]
    },
    aButton: {
      keyboard: ["space"],
      gamepad: ["south", "ltrigger", "rtrigger"]
    }
  }
});

k.loadRoot("./"); // A good idea for Itch.io publishing later

// !notes
// do not access the varieables as global
// use tag and make local variable

// loadSprite here
k.loadSprite("map", "maps/map.png")
k.loadSprite("bean", "sprites/bean.png");

// !scene
k.scene("gameloop", () => {

  k.add([
    k.sprite("map"),
    k.pos()
  ])

  let player = createPlayer(k)
})

k.go("gameloop")
