export function createPlayer(k) {
  const player = k.add([
    k.pos(120, 80), 
    k.sprite("bean"), 
    k.area(),  // Enable collision detection
    k.body(),  // Enable physics-like movement
    "player", 
    {
      speed: 7000,  // Adjusted to a reasonable speed
      dir: k.vec2(0, 0),  // Initial direction
    }
  ]);

  // Handle movement using onButtonDown (continuous movement)
  k.onButtonDown("up", () => {
    player.move(0, -player.speed * k.dt());
  });

  k.onButtonDown("down", () => {
    player.move(0, player.speed * k.dt());
  });

  k.onButtonDown("left", () => {
    player.move(-player.speed * k.dt(), 0);
  });

  k.onButtonDown("right", () => {
    player.move(player.speed * k.dt(), 0);
  });

  return player;  // Return player so it can be used in other files
}
